@import "_variables";
@import "_typography";

// nav
nav ul a {
    display: block;
}

/* The sidebar menu */
.sidebar {
    height: 100%; /* 100% Full-height */
    width: 448px; /* 0 width - change this with JavaScript */
    position: fixed; /* Stay in place */
    z-index: 6; /* Stay on top */
    top: 0;
    margin-left: -448px;
    background-color: $color-black; /* Black*/
    overflow-x: hidden; /* Disable horizontal scroll */
    transition: 0.5s; /* 0.5 second transition effect to slide in the sidebar */
  }
  
/* The sidebar links */
.sidebar a {
  padding: 24px 32px 24px 56px;
  text-decoration: none;
  display: block;
  transition: 0.3s;
  #SidebarLink {
      box-shadow:inset 0px 1px #4e4e4e;
  }
  h3, h4 {
      font-family: "freight-big-pro", serif;
      font-weight: 400;
      margin: 0;
      transition: 0.3s;
  }
  h4 {
      color: $color-slate;
      font-style: normal;
      font-size: 1.125rem;
      margin-bottom: 8px;
  }
  h3 {
      color: $color-slate;
      font-style: normal;
      font-size: 1.125rem;
  }
  h5 {
      font-family: "neue-haas-grotesk-display", sans-serif;
      font-weight: 600;
      color: #3E3E3E;
      text-align: right;
      line-height: 0;
      float: right;
      margin-top: 10px;
      font-size: 0.75rem;
      text-transform: uppercase;
  }
}

/* When you mouse over the navigation links, change their color */
.sidebar #SidebarLink a:hover {
  transition: 0.3s ease-out;
  cursor: url(/assets/svg/expand.svg) 15 15, auto;
  h4, h5 {
      color: $color-white;
      margin-left: 5px;
      transition: 0.3s ease-out;
  }
  h3 {
      color: $color-white;
      margin-left: 10px;
      transition: 0.3s ease;
  }
}

#mySidebarList:hover {
  cursor: url(/assets/svg/expand.svg) 15 15, auto;
}

/* Position and style the close button (top right corner) */
.sidebar .closebtn {
  position: relative;
  padding: 0;
  margin: 24px;
  color: $color-white;
}

.sidebar .closebtn::before {
  content: url(/assets/svg/close.svg);
  fill: $color-white;
  margin-right: 10px;
}

.sidebar ul {
    padding-left: 0px;
    margin: 0;
}

.menu-button {
  padding: 0px;
  display: flex;
  align-items: center;

  img:first-child {
    margin-right: 10px;
  }
}

// .openbtn:hover {
//   background-color: #444;
// }

/* Style page content - use this if you want to push the page content to the right when you open the side navigation */
#sidebar_main {
  position: fixed;
  z-index: 5;
  top: 0px;
  transition: margin-left .5s; /* If you want a transition effect */
  mix-blend-mode: difference;

  .menu-button {
    margin: 24px 0px 24px 56px;
  }
}

  
/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidebar {padding-top: 15px;}
  .sidebar a {font-size: 18px;}
}

#SidebarLink {
    border-top: solid #4E4E4E 1px;
    border-bottom: solid #000000 1px;
}

#SidebarLink:hover {
    border-top: solid $color-slate 1px;
    border-bottom: solid $color-slate 1px;
    transition: 0.3s ease-out;   
}

// prev/next page navigation
.chapter_jump_button {
  text-decoration: none;
  left: 4vw;
  width: 30vw;
}

#jump_next {
  position: absolute;
  bottom: 2vh;
}

#jump_prev {
  position: absolute;
  top: 10vh;
}

.chapter_jump_nav {
  position: fixed;
  mix-blend-mode: difference;
  height: 100vh;
  z-index: 4;
  left: 0;
  top: 0;
}